import loadErr from '@/components/loadErr.vue'

export default {
	data() {
		return {
			dataArr: [],
			ticketArr: [], //买票电话
			isLoadErr: false,
			isShowCallBox: false
		}
	},
	components: { loadErr },
	computed: {
		pageMinH() {
			let sys = uni.getSystemInfoSync()
			return sys.windowHeight + 'px'
		}
	},
	onLoad() {
		this.getOrderData()
	},
	methods: {
		toShowHotCall() {
			this.isShowCallBox = true
		},
		toCloseCallBox(e) {
			let sign = e.target.dataset.sign
			if(sign) {
				this.isShowCallBox = false
			}
		},
		toCallTel(tel) {
			uni.makePhoneCall({
				phoneNumber: tel
			})
		},
		toJumpOrderDetail(idx) {
			const $this = this
			if(this.dataArr[idx].order_type == 0) {
				uni.navigateTo({
					url: `/pages/orderDetail/orderDetail?id=${$this.dataArr[idx].order_id}`
				})
			}else {
				uni.navigateTo({
					url: `/pages/cfDetail/cfDetail?id=${$this.dataArr[idx].crowd_id}`
				})
			}
		},
		toRelaodData() {
			this.dataArr = []
			this.isLoaderr = false
			this.getOrderData()
		},
		getOrderData() {
			uni.showLoading({
				title: '数据加载中',
				mask: true
			})
			this.$api.ajax({
				url: this.$config.orderList
			})
			.then((res) => {
				if(res.statusCode == 200 && res.data.code == 0) {
					this.dataArr = res.data.data.order_list
					this.ticketArr = res.data.data.ticket_list
					setTimeout(() => {
						uni.hideLoading();
					}, 500)
				}else {
					uni.hideLoading();
					this.$api.warnNotice(res.data.message)
					this.isLoadErr = true
				}
			})
			.catch((err) => {
				uni.hideLoading();
				this.isLoadErr = true
			})
		}
	}
}
